.poster{
  width: 100%;
  height: 80vh;
  display: flex;
  background-color: skyblue;
  padding: 0 20px;

}
.poster h1{
  font-size: 100px;
}


.poster img{
  width: 500px;
  height: fit-content;
}

.poster button{
  background-color: blue;
  color: white;
  font-size: larger;
  font-weight: bold;
  border: none;
  width: 200px;
  height: 100px;
}


/* ///////////////boxes */
.boxes{
  display: flex;
  justify-content: space-around;
  padding: 0 100px;
  margin-top: -100px;
  z-index: 111;
}


.box{
  width: 350px;
  height: 350px;
  background-color: darkblue;
}

@media screen and (max-width: 700px) {
  .body{
    display: grid;
  }
}